"use client";

import React, { useState, useRef, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Bot, Settings2, History, MoreHorizontal, Zap } from 'lucide-react';
import ModeSelector from './ModeSelector';
import ChatMessage from './ChatMessage';
import ChatInput from './ChatInput';

type Message = {
  id: string;
  role: 'agent' | 'user';
  content: string;
  timestamp: string;
  status?: 'sending' | 'sent' | 'read';
  actions?: string[];
};

const INITIAL_MESSAGES: Message[] = [
  {
    id: '1',
    role: 'agent',
    content:
      "Good afternoon. I've analyzed your current pipeline. There are 3 leads requiring immediate attention to close before Q4 ends. Shall we review the negotiation strategy for Acme Corp?",
    timestamp: '10:42 AM',
    status: 'read',
  },
  {
    id: '2',
    role: 'user',
    content: 'Yes, pull up the latest engagement metrics for Acme.',
    timestamp: '10:43 AM',
    status: 'read',
  },
  {
    id: '3',
    role: 'agent',
    content:
      'Acme Corp engagement is up 45% this week. Key stakeholder (CTO) viewed the pricing page 3 times yesterday. I recommend proposing the Enterprise tier with the early-adopter discount.',
    timestamp: '10:43 AM',
    status: 'read',
    actions: ['Draft Email', 'View Metrics'],
  },
];

export default function SalesAgentUI() {
  const [activeMode, setActiveMode] = useState<'chat' | 'configure' | 'history'>('chat');
  const [messages, setMessages] = useState<Message[]>(INITIAL_MESSAGES);
  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }
  }, [messages]);

  const handleSendMessage = (text: string) => {
    const newMessage: Message = {
      id: Date.now().toString(),
      role: 'user',
      content: text,
      timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
      status: 'sending',
    };

    setMessages((prev) => [...prev, newMessage]);

    setTimeout(() => {
      setMessages((prev) =>
        prev.map((m) => (m.id === newMessage.id ? { ...m, status: 'sent' } : m))
      );
    }, 800);

    setTimeout(() => {
      setMessages((prev) => [
        ...prev,
        {
          id: (Date.now() + 1).toString(),
          role: 'agent',
          content:
            "I've updated the deal probability to 85%. Would you like me to schedule a follow-up?",
          timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
        },
      ]);
    }, 1800);
  };

  return (
    <div className="flex h-full w-full flex-col overflow-hidden bg-zinc-950">
      <header className="flex h-16 items-center justify-between border-b border-zinc-800 px-6">
        <div className="flex items-center gap-3">
          <div className="flex h-8 w-8 items-center justify-center rounded-lg bg-indigo-500/10 text-indigo-400">
            <Bot size={18} />
          </div>
          <div>
            <h1 className="text-sm font-semibold">Sales Assistant AI</h1>
            <p className="text-xs text-zinc-500">Online â€¢ v2.4.0</p>
          </div>
        </div>

        <ModeSelector currentMode={activeMode} onModeChange={setActiveMode} />

        <div className="flex items-center gap-2">
          <History size={18} className="text-zinc-400" />
          <Settings2 size={18} className="text-zinc-400" />
          <MoreHorizontal size={18} className="text-zinc-400" />
        </div>
      </header>

      <div className="flex-1 overflow-y-auto px-4 py-6" ref={scrollRef}>
        <div className="mx-auto max-w-3xl space-y-6">
          {messages.map((msg) => (
            <ChatMessage key={msg.id} message={msg} />
          ))}
        </div>
      </div>

      <div className="border-t border-zinc-800 p-4">
        <div className="mx-auto max-w-3xl">
          <ChatInput onSend={handleSendMessage} />
          <div className="mt-2 flex items-center justify-center gap-2 text-xs text-zinc-500">
            <Zap size={12} />
            AI responses should be verified
          </div>
        </div>
      </div>
    </div>
  );
}
