"use client";

import React from 'react';
import { motion } from 'framer-motion';
import { MessageSquare, Sliders, BarChart3 } from 'lucide-react';

type Mode = 'chat' | 'configure' | 'history';

export default function ModeSelector({
  currentMode,
  onModeChange,
}: {
  currentMode: Mode;
  onModeChange: (mode: Mode) => void;
}) {
  const modes = [
    { id: 'chat', label: 'Chat', icon: <MessageSquare size={14} /> },
    { id: 'configure', label: 'Config', icon: <Sliders size={14} /> },
    { id: 'history', label: 'Analytics', icon: <BarChart3 size={14} /> },
  ];

  return (
    <div className="flex rounded-lg bg-zinc-900 p-1">
      {modes.map((mode) => (
        <button
          key={mode.id}
          onClick={() => onModeChange(mode.id as Mode)}
          className="relative px-3 py-1.5 text-sm text-zinc-400"
        >
          {currentMode === mode.id && (
            <motion.div
              layoutId="mode"
              className="absolute inset-0 rounded-md bg-zinc-800"
            />
          )}
          <span className="relative z-10 flex items-center gap-2">
            {mode.icon}
            {mode.label}
          </span>
        </button>
      ))}
    </div>
  );
}
